.loader-container {

}

@-webkit-keyframes spin
{
    0% { -webkit-transform: rotate(0); transform: rotate(0);}
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin
{
    0% { -webkit-transform: rotate(0); transform: rotate(0);}
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.loader
{
    position: absolute;
    height: 100px;
    width: 100px;
    top: 50%;
    left: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-left: -50px;
    margin-top: -50px;
    border: calc(100px * 0.07) solid;
    border-color: #007EFF #f2f2f3 #f2f2f3 #f2f2f3;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 calc(100px * 0.80) #007EFF;
            box-shadow: 0 0 calc(100px * 0.80) #007EFF;
    opacity: 0.9;
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear;
    background-color: #EAEBEC;
}



