@import 'https://fonts.googleapis.com/css?family=Abril+Fatface|Roboto';

.profile-info-container {
    margin-top: 70px;
    margin-bottom: 20px;
    position: absolute;
    width: 100%;
}

.Profile-Professional-Information {
    padding-right: 1%;
    padding-left: 1%;
    vertical-align: top;
}

.Profile-Headers {
    width: 90%;
    display: inline-block;
    font-size: 36px;
    text-align: center;
    text-decoration: underline;
    -webkit-text-decoration-color: #007EFF;
            text-decoration-color: #007EFF;
}

.Request-Access {
    width: 20%;
    font-size: 20px;
    text-align: center;
    border: 1px solid #007EFF;
    background-color: #BFBFBF;
    border-radius: 20px;
    margin-left: 75%;
    cursor: pointer;
}

.App-Images {
    width: 10%;
    padding: 1%
}

.profile-place {
    width: 80%;
    height: 80%;
    position: absolute;
    top: calc(50% + 25px);
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


